	module.exports = {"data":{"markdownRemark":{"html":"<p>The Ternary operator replaces an <code>if</code>/<code>else</code> block in a condensed format. The following is the general format of the ternary operator.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>condition ? expr1 : expr2</code></pre>\n      </div>\n<h2>Description</h2>\n<p>If condition is true, the operator resolves to the value of expr1; otherwise, it resolves to the value of expr2. </p>\n<p>For example, to display a different message based on the value of the isMember variable, you could use this statement:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">let</span> isMember <span class=\"token operator\">=</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">let</span> message <span class=\"token operator\">=</span> isMember <span class=\"token operator\">?</span> <span class=\"token string\">'Welcome Back!'</span> <span class=\"token punctuation\">:</span> <span class=\"token string\">'You need to login'</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// 'Welcome Back'</span>\n</code></pre>\n      </div>\n<p>Another useful method to using a Ternary operator would be to envoke it to conditionally execute a function or method </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>    \n    <span class=\"token keyword\">function</span> <span class=\"token function\">memberOpen</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n        console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"open\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">function</span> <span class=\"token function\">memberClose</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n        console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"close\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">let</span> isMember <span class=\"token operator\">=</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">;</span>\n    \n    <span class=\"token punctuation\">(</span>isMember<span class=\"token punctuation\">)</span> <span class=\"token operator\">?</span> <span class=\"token function\">memberOpen</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">:</span> <span class=\"token function\">memberClose</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/M8Ge/1' target='_blank' rel='nofollow'>Run Code</a></p>\n<h2>Running functions with ternary operator</h2>\n<p>It’s also possible to run functions using ternary operator, which sometimes can be useful and more readable. However, use it carefully, because then code is harder to debug.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>    <span class=\"token keyword\">const</span> runFirst <span class=\"token operator\">=</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">;</span>\n    runFirst <span class=\"token operator\">?</span> <span class=\"token function\">first</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">:</span> <span class=\"token function\">second</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p><strong>Read more:</strong> <a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Conditional_Operator' target='_blank' rel='nofollow'>MDN</a></p>","fields":{"slug":"/javascript/ternary-operator/"},"frontmatter":{"title":"Ternary Operator"}}},"pathContext":{"slug":"/javascript/ternary-operator/"}}

